<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图 -->
    <el-card class="box-card">
      <!-- 输入内容 -->
      <el-row :gutter="20">
        <el-col :span="8">
          <div>
            <el-input placeholder="请输入内容" class="input-with-select">
              <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>
          </div>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"
            >添加用户</el-button
          >
        </el-col>
      </el-row>

      <!-- <div v-for="o in 4" :key="o" class="text item">
        {{'列表内容 ' + o }}
      </div> -->
      <el-table :data="tableData" stripe style="width: 100%" border>
        <el-table-column type="index"> </el-table-column>
        <el-table-column prop="name" label="姓名" width="180">
        </el-table-column>
        <el-table-column prop="mailbox" label="邮箱"> </el-table-column>
        <el-table-column prop="phone" label="电话"> </el-table-column>
        <el-table-column prop="role" label="角色"> </el-table-column>
        <el-table-column prop="state" label="状态">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.state"> </el-switch>
          </template>
        </el-table-column>
        <el-table-column prop="work" label="操作" width="180">
          <template>
            <el-button type="primary" icon="el-icon-edit" circle></el-button>
            <el-button type="danger" icon="el-icon-delete" circle></el-button>
            <el-tooltip effect="dark" content="发送邮件" placement="top">
              <el-button
                type="warning"
                icon="el-icon-message"
                circle
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="1"
        :page-sizes="[1, 2, 3, 4]"
        :page-size="1"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page"
      >
      </el-pagination>
    </el-card>

    <!-- 对话框 -->
    <el-dialog
      title="添加用户"
      :visible.sync="addDialogVisible"
      width="600px"
      @close="addDialogClosed"
    >
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRefs"
        label-width="70px"
      >
        <el-form-item label="用户名" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="mailbox">
          <el-input v-model="addForm.mailbox"></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="phone">
          <el-input v-model="addForm.phone"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUser">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data: function() {
    //验证邮箱的规则

    var checkEmail = (rule, value, cb) => {
      const regEmail = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (regEmail.test(value)) {
        return cb();
      }
      cb(new Error("请输入合法的邮箱"));
    };
    //验证手机的规则
    var checkMobile = (rule, value, cb) => {
      const regMobile = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
      if (regMobile.test(value)) {
        return cb();
      }
      cb(new Error("请输入合法的手机号码"));
    };
    return {
      addDialogVisible: false,
      addForm: {
        username: "",
        password: "",
        mailbox: "",
        phone: ""
      },
      addFormRules: {
        username: [
          { required: true, message: "请输入用户邮箱", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "用户名的长度在3~10个字符串",
            trigger: "blur"
          }
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
        mailbox: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" }
        ],
        phone: [
          { required: true, message: "请输入手机", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" }
        ]
      },
      tableData: [
        {
          name: "尧月",
          mailbox: "1298295230@qq.com",
          phone: "10086",
          role: "最高级别",
          state: true,
          work: "最高"
        },
        {
          name: "尧月",
          mailbox: "1298295230@qq.com",
          phone: "10086",
          role: "最高级别",
          state: true,
          work: "最高"
        },
        {
          name: "尧月",
          mailbox: "1298295230@qq.com",
          phone: "10086",
          role: "最高级别",
          state: true,
          work: "最高"
        },
        {
          name: "尧月",
          mailbox: "1298295230@qq.com",
          phone: "10086",
          role: "最高级别",
          state: true,
          work: "最高"
        }
      ],
      page: 4
    };
  },
  methods: {
    handleSizeChange(newSize) {
      console.log(newSize);
      console.log(this.tableData.length);
    },
    handleCurrentChange(newPage) {
      console.log(newPage);
    },
    //监听添加对话框的关闭事件
    addDialogClosed() {
      this.$refs.addFormRefs.resetFields();
    },
    addUser() {
      // this.$refs.addFormRefs.validate(vaild => {
      //   if (!valid) return;
      //   this.tableData.push({
      //     name: this.addForm.username,
      //     mailbox: this.addForm.mailbox,
      //     phone: this.addForm.phone,
      //     role: "最高级别",
      //     state: true,
      //     work: "最高"
      //   });
      // });
    }
  }
};
</script>

<style lang="css" scoped>
.el-breadcrumb {
  margin-bottom: 15px;
}

.el-card {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
}

.el-table {
  margin-top: 15px;
  font-size: 12px;
}

.el-pagination {
  margin-top: 15px;
}
</style>
