import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from './../components/Login.vue'
import Home from './../components/home.vue';
import Welcome from './../components/Welcome.vue'
import Users from './../components/users.vue'
import Power from './../components/power.vue'
import Roles from './../components/roles.vue'
import Categaries from './../components/categaries.vue'
import Parser from './../components/params.vue'
import Shoplist from './../components/shoplist.vue'
import Addshop from './../components/addshop.vue'
import Orderlist from './../components/orderlist.vue'
import Datalist from './../components/datalist.vue'

Vue.use(VueRouter)

const routes = [
  //路由重定向
  {path:'/',redirect:'/login'},
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {

    path: '/home',
    name: 'home',
    component: Home,
    redirect:'/welcome',
    children:[
      {path:'/welcome',component:Welcome},
      {path:'/users',component:Users},
      {path:'/power',component:Power},
      {path:'/roles',component:Roles},
      {path:'/categaries',component:Categaries},
      {path:'/parser',component:Parser},
      {path:'/shoplist',component:Shoplist},
      {path:'/addshop',component:Addshop},
      {path:'/orderlist',component:Orderlist},
      {path:'/datalist',component:Datalist},
    ]
  }

]

const router = new VueRouter({
  routes
})

export default router
